import { useDispatch } from 'react-redux'

import { REFRESH_META } from './actions.js'

// Recalculates the current page's metadata.
//
// Acadeum Course Share website uses this hook because it patches
// the page `Component`'s `.meta()` function in real time based on
// whether the user is authenticated or not.
//
export default function useRefreshMeta() {
	const dispatch = useDispatch()
	return function refreshMeta() {
		dispatch({ type: REFRESH_META })
	}
}